import React, { useEffect } from "react";
import UserStore from "../../../UserStore";
const Modal = (props) => {
  const divStyle = {
    display: props.displayModal ? "block" : "none",
  };
  function closeModal(e) {
    e.stopPropagation();
    props.closeModal();
  }
  useEffect(() => {
    const script = document.createElement("script");
    script.src =
      "https://api.ravepay.co/flwv3-pug/getpaidx/api/flwpbf-inline.js";
    script.async = true;
    document.body.appendChild(script);
  });
  return (
    <div className="modal" onClick={closeModal} style={divStyle}>
      {" "}
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        {" "}
        <span className="close" onClick={closeModal}>
          &times;
        </span>{" "}
        <div className="contentent" style={{ width: "100% !important" }}>
          <form className="forming">
            <a
              class="flwpug_getpaid"
              data-PBFPubKey="FLWPUBK-4c45fc2e72a23d406b83f7439f70e3d6-X"
              data-txref="rave-123456"
              data-amount={UserStore.amount}
              data-customer_email={UserStore.username + "@box1.ng"}
              data-currency="NGN"
              data-pay_button_text="Proceed to Pay Now"
              data-country="NG"
              data-redirect_url="/thanks"
            ></a>
            <p>
              Please complete your payment, so we can send our rider in short
              time!
            </p>
          </form>
        </div>
      </div>{" "}
    </div>
  );
};
export default Modal;
