  async componentDidMount() {
    const script = document.createElement("script");
    script.src =
      "https://api.ravepay.co/flwv3-pug/getpaidx/api/flwpbf-inline.js";
    script.async = true;
    document.body.appendChild(script);
    window.addEventListener("resize", this.resize);
  }
  async ScheduleShipment() {
    if (
      this.state.plocation !== "" &&
      this.state.dlocation !== "" &&
      this.state.rname !== "" &&
      this.state.rphone !== "" &&
      this.state.itemtype !== "" &&
      this.state.itemweight !== "" &&
      this.state.amount !== "" &&
      this.state.payment !== ""
    ) {
      try {
        this.setState({ loadings: true });
        let res = await fetch("/scheduledomestic", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            sessionID: UserStore.sessionID,
            senderphone: UserStore.username,
            sendername: UserStore.name,
            from: UserStore.state,
            to: UserStore.state,
            date: "",
            time: "",
            plocation: this.state.plocation,
            dlocation: this.state.dlocation,
            rname: this.state.rname,
            rphone: this.state.rphone,
            itemtype: this.state.itemtype,
            itemweight: this.state.itemweight,
            amount: this.state.amount,
            payment: this.state.payment,
          }),
        });

        let result = await res.json();
        if (result && result.success) {
          UserStore.amount = this.state.amount;
          this.setState({
            confirm: result.confirm,
          });
          this.setState({ loading: false });
          if (this.state.confirm === "yes") {
            return;
          } else {
            return alert(
              "Congratulations, your pickup is scheduled successfully"
            );
          }
        } else if (result && result.success === false) {
          this.setState({ loading: false });
          alert(result.msg);
        }
      } catch (e) {
        console.log(e);
      }
    } else {
      return alert("Please fill out your form completely");
    }
  }
  async RequestPickupCard() {
    if (
      this.state.plocation !== "" &&
      this.state.dlocation !== "" &&
      this.state.amount !== "" &&
      this.state.rname !== "" &&
      this.state.rphone !== "" &&
      this.state.rward !== "" &&
      this.state.sward !== ""
    ) {
      try {
        setTimeout(() => {
          this.setState({ loading: true });
        }, 300);
        let res = await fetch("/requestpickupcard", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            username: UserStore.username,
            sname: UserStore.name,
            fromlocation: UserStore.state,
            sward: this.state.sward,
            plocation: this.state.plocation,
            rname: this.state.rname,
            rphone: this.state.rphone,
            tolocation: UserStore.state,
            rward: this.state.rward,
            dlocation: this.state.dlocation,
            itemtype: this.state.itemtype,
            amount: this.state.amount,
            paymenttype: this.state.paymentcard,
          }),
        });

        let result = await res.json();
        if (result && result.success) {
          UserStore.amount = this.state.amount;
          setTimeout(() => {
            this.setState({ modal: true });
            this.setState({ loading: false });
          }, 300);
          return;
        } else if (result && result.success === false) {
          this.setState({ loading: false });
          alert(result.msg);
        }
      } catch (e) {
        console.log(e);
      }
    } else {
      alert("Please Fill out your form Correctly");
    }
  }
